import React, { Component } from 'react';
import {Grid, Row, Col} from 'react-flexbox-grid';
import YouTube from 'react-youtube';
import sorty from './sorty.svg';
import lulzface from './lulzface.svg';

import FiveStars from './fivestars';
import Profile from './profile';

import jonchin from './jonchin.jpg';
import earlco from './earlco.jpg';
import rishubnahar from './rishubnahar.jpg';

class App extends Component {
    render() {
	var profile_data = [
	    {
		name: 'Jon Chin',
		bio: `<p>Jon Chin is an educator and a poet, 2nd dan blackbelt, software engineer, and social entrepreneur. His greatest concern is how to leverage breakthrough technology to address the human condition in illuminating ways. Jon's classrooms are often attended by laughter, sometimes so much that theyre asked to pipe down.</p>

<p>He loves what he does for work; Jon teaches literature, computer science, math, and martial arts to ages between 5 and 65. He has taught in high risk neighborhoods in New York City, including Rikers Island. And he loves every minute, every lesson.</p>`,
		photo: jonchin
	    },
	    {
		name: 'Earl Co',
		bio: '<p>Earl Co graduated BS in Computer Engineering from NYU Tandon School of Engineering. Earl currently works as a full-time Educational Technologist at NYU Tandon Online, doing video production and course development for online learning students. His interests in many fields include augmented and virtual reality, autonomous robotics and machine learning, commercial space travel, and entrepreneurship. He fixes phones and computers on the side, and he has many unfinished projects.</p>',
		photo: earlco
	    },
	    {
		name: 'Rishub Nahar',
		bio: '<p>Rishub Nahar is a student at NYU Stern School of Business. He is planning to double major in finance and computer science. His goal is to use technology to expand financial services to all. His major interests are economic policy, web development, machine learning, and the financial markets.</p>',
		photo: rishubnahar
	    }
	];

	var profiles = profile_data.map(function(data){
	    return <Profile name={data.name}
	    bio={data.bio}
	    photo={data.photo} />
	});
	return (
	    <div>
	    <div style={{backgroundColor: '#57068c',
			 color: '#eee'}}>
	    <Grid>
		<Row middle="xs"
		     style={{padding: '5em 0'}}>
		    <Col sm={2}>
			<img src={lulzface} alt="lulz" />
		    </Col>
		    <Col smOffset={1} sm={9}>
			<img src={sorty} alt="We are Sorty McSortface" />
			<h1 className="center">
			    A computer vision powered automated recycling solution
			</h1>
		    </Col>
		</Row>
	    </Grid>
	    </div>
	    <Grid>
		<Row middle="xs"
		     style={{marginTop: '5em'}}>
		    <Col sm={6}>
			<div className="embed-responsive">
			    <YouTube
				videoId={'b9gjsGgpY4c'}
				opts={{t: '2679s'}}
			    />
			</div>
		    </Col>
		    <Col sm={6} className="center">
			<h1>PennApps XVI Grand Prize Winner</h1>
			<FiveStars />
			<br /><br />
			<h1>Facebook's Favorite Hack</h1>
			<FiveStars />
		    </Col>
		</Row>
		<h1 className="center"
		    style={{margin: '1em 0',
		    fontSize: '4em'}}>
		    Our Team
		</h1>
		{profiles}
	    </Grid>
	    </div>
	);
    }
}

export default App;



