import React, { Component } from 'react';
import star from './star.svg';

export default class FiveStars extends Component {
    render() {
	const n = 5; // Or something else
	var stars = [...Array(n)].map((e, i) =>
	    <img src={star} style={{width: '2em', height: 'auto'}}
		 alt="star" />
	)
	return (
	    <div>
		{stars}
	    </div>
	);
    }
}



